@import "sass/index";

.header {
  display: flex;
  justify-content: space-between;

  font-family: "Gilroy";
  color: use(--primary-light);
  padding-bottom: 20px;
}

.button {
  font-weight: 600;
  font-size: 16px;
  cursor: pointer;
  width: fit-content;
  display: flex;
  align-items: center;
}

.provider {
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  cursor: pointer;
  width: fit-content;
  color: use(--primary-light) !important;
  text-decoration: none;
  margin-right: 5px;

  & > strong {
    text-transform: uppercase;
  }
}
