@import "sass/index";

.root {
  color: use(--primary-light);
  font-family: "Gilroy";
  margin-top: 20px;
}
.titleWrapper {
  display: flex;
  min-height: 40px;
}

.text {
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
}

.type {
  padding: 6px 15px;
  background: use(--info-dark);
  border-radius: 10px;
  width: fit-content;
  height: fit-content;

  margin-left: 40px;
}
.description {
  background: use(--info-dark);

  border-radius: 10px;
  border-top-left-radius: 0;
  padding: 15px;
}
.decoration {
  max-height: 40px;
  height: auto;
  padding: 5px 10px;
}
